// export default App;
import React from 'react';
import { AuthProvider } from "./contexts/authContext";
import SmartAss from './components/SmartAss';
import { TrivialPursuit, TrivialPursuitWithCats } from './components/TrivialPursuit';
//import OpenAIChat from './components/OpenAIChat';
import Header from './components/Header';
import './App.css'; // Import CSS for styling

// Updated components object with descriptions
const components = {
    //'AI Chat': { component: <OpenAIChat />, description: "AI-powered chat interface" },
    'SmartAss': { component: <SmartAss />, description: "Smart\nAss" },
    'TPWC': { component: <TrivialPursuitWithCats />, description: "Trivial Pursuit\nwith Categories" },
    'TP': { component: <TrivialPursuit />, description: "Trivial Pursuit\nClassic" }
};

const App = () => {
    // State to manage the active component and description
    const [activeComponent, setActiveComponent] = React.useState(null);
    //const [description, setDescription] = React.useState('');

    // Function to handle button click
    const handleComponentSelect = (key) => {
        setActiveComponent(key); // Set active component
        //setDescription(components[key].description); // Set description
    };

    return (
        <AuthProvider>
            <div>
                <Header />
                <h2>Pick Your Game!</h2>
                <div style={{ display: 'flex', gap: '10px', margin: '20px 20px 20px 20px', justifyContent: 'space-between' }}>
                    {Object.keys(components).map((key) => (
                        <button
                            key={key}
                            className='gameSelect'
                            id={key}
                            onClick={() => handleComponentSelect(key)}
                        >
                            {/* {components[key].description} */}
                            {components[key].description.split('\n').map((line, index) => (
                                <p key={index}>{line}</p>
                            ))}
                        </button>
                    ))}
                </div>
                {/* Display the description */}
                {/* {description && <p>{description}</p>} */}
                {/* Display the selected component */}
                <div>
                    {activeComponent ? components[activeComponent].component : <p>Select an option above</p>}
                </div>
            </div>
        </AuthProvider>
    );
};

export default App;
