const TrivialPursuit = () => {
  const traditionalCategories = [
    'Science & Nature', 'Art & Literature', 'History', 'Geography', 'Entertainment', 'Sports'
  ];

  const { currentUser } = useAuth();

  const [modalContent, setModalContent] = useState(null);
  const [data, setData] = useState([]);
  const [randomQuestion, setRandomQuestion] = useState(null);
  const [questionCount, setQuestionCount] = useState(1);

  // // Function to handle button clicks
  // const handleClick = (type) => {
  //   // Filter questions by category
  //   const filteredQuestions = data.filter(item => item.Category === type);
  //   if (filteredQuestions.length > 0) {
  //     // Select a random question from the filtered list
  //     const randomQ = filteredQuestions[Math.floor(Math.random() * filteredQuestions.length)];
  //     setRandomQuestion(randomQ);
  //     setModalContent(`${randomQ.Category}\nQ:${randomQ.Question} \nA: ${randomQ.Answer}`);
      
  //   } else {
  //     setModalContent('No questions available for this category.');
  //   }
  // };

  const handleClick = async (type) => {
    let questionToDisplay;
    //console.log(questionCount)
    if (currentUser && currentUser.email === 'pilky100@gmail.com' && questionCount % 10 === 0) {
      //console.log('AI')
      try {
        const generateTrivia = httpsCallable(functions, 'generate_trivia');
        const result = await generateTrivia({ 
          category: type, 
          role_content: "You are a trivial pursuit question generator. Given the prompted category, generate one question and its answer and return the results in JSON"
        });

        if (result.data.error) {
          setModalContent(result.data.error);
        } else {
          const parsedQuestions = JSON.parse(result.data.questions);
          //console.log(parsedQuestions)
          //questionToDisplay = parsedQuestions[0];
          
          setModalContent(`${type}\n ${parsedQuestions.question}\n ${parsedQuestions.answer}`);

          saveTP(
            "TrivialPursuit", // dbRefPath
            type, // category
            parsedQuestions.question, // question
            parsedQuestions.answer    // answer
          );

        }
      } catch (err) {
        setModalContent('Failed to fetch AI-generated questions. Please try again.');
        console.error(err);
        return;
      }
    } else {
      //console.log('FB')
      const filteredQuestions = data.filter(item => item.Category === type);
      if (filteredQuestions.length > 0) {
        questionToDisplay = filteredQuestions[Math.floor(Math.random() * filteredQuestions.length)];
        setRandomQuestion(questionToDisplay)
        setModalContent(`${questionToDisplay.Category}\nQ:${questionToDisplay.Question} \nA: ${questionToDisplay.Answer}`);
      } else {
        setModalContent('No questions available for this category.');
        return;
      }
    }

    if (questionToDisplay) {
      setQuestionCount(questionCount + 1);
    }
  };

  //return `${key}\nQ: ${question.Question}\nA: ${question.Answer}`

  const flagQuestion = () => {
    console.log({randomQuestion});
    saveData(
      "flaggedQuestions", // dbRefPath
      "TrivialPursuit",          // game
      randomQuestion.Category, // category
      randomQuestion.Question, // question
      randomQuestion.Answer    // answer
    );
  };

  useEffect(() => {
    const fetchSmartAssData = async () => {
      try {
        const result = await fetchData("TrivialPursuit");
        //console.log('Fetched Data:', result);
        //console.log(result)
        setData(result);
      } catch (error) {
        console.error("Failed to fetch TP data:", error);
      }
    };

    fetchSmartAssData();
  }, []);

  return (
    <div>
      <h1>Trivial Pursuit</h1>
      <div className='card-container'>
        {/* Buttons */}
        {traditionalCategories.map((category, index) => (
          <button 
            key={index} 
            className="card-button" 
            id={category.toLowerCase().replace(/\s+/g, '').replace('&','')} 
            onClick={() => handleClick(category)}
          >
            {category}
          </button>
        ))}

        {/* Modal Popup */}
        {modalContent && (
          <div className="modal">
            <div className="modal-content">
            <button
            style={{
              marginLeft: '5px',
              color: '#007BFF',
              textDecoration: 'none',
              cursor: 'pointer',
              border: 'none',
              background: 'none',
              position: 'absolute',
              left: '0', 

          }} onClick={flagQuestion}
            
            > Flag Question</button>
              <span onClick={() => setModalContent(null)} className="close">&times;</span>
              {modalContent.split('\n').map((line, index) => (
                <p key={index}>{line}</p>
              ))}
            </div>
          </div>
        )}
      </div>
    </div>
  );
};



