const TrivialPursuitWithCats = () => {
  const [modalContent, setModalContent] = useState(null);
  const [categories, setCategories] = useState([]);
  const [displayedCategories, setDisplayedCategories] = useState([]);
  const [data, setData] = useState({});

  // Function to randomly select 1 new category
  const selectRandomCategory = (exclude) => {
    let filtered = categories.filter((cat) => cat !== exclude); // Exclude current category
    const randomIndex = Math.floor(Math.random() * filtered.length);
    return filtered[randomIndex]; // Pick a new random category
  };

  // Function to handle button clicks
  const handleClick = (category) => {
    const questions = data[category];
    const content = Object.keys(questions).map((key) => {
      const question = questions[key];
      return `${key}\nQ: ${question.Question}\nA: ${question.Answer}`;
    }).join('\n\n');

    setModalContent({
      category: category,
      content: content,
    });
  };

  // Function to handle modal close and update the category
  const handleCloseModalAndUpdate = () => {
    if (modalContent) {
      const newCategory = selectRandomCategory(modalContent.category);

      // Replace only the selected category in the displayedCategories array
      setDisplayedCategories((prev) =>
        prev.map((cat) =>
          cat === modalContent.category ? newCategory : cat
        )
      );
    }

    setModalContent(null); // Close modal
  };

  // Function to handle modal close without updating categories
  const handleCloseModal = () => {
    setModalContent(null); // Simply close the modal
  };

  useEffect(() => {
    const fetchSmartAssData = async () => {
      try {
        const result = await fetchData("TrivialPursuitWithCats");

        // Flatten nested levels to get all categories
        const flattenedData = Object.values(result).reduce((acc, item) => {
          return { ...acc, ...item };
        }, {});

        setData(flattenedData);

        // Extract categories (e.g., "Harry Potter")
        const categoryKeys = Object.keys(flattenedData);
        setCategories(categoryKeys);

        // Randomly pick 4 categories
        const shuffled = categoryKeys.sort(() => 0.5 - Math.random()).slice(0, 4);
        setDisplayedCategories(shuffled);
      } catch (error) {
        console.error("Failed to fetch TPWC data:", error);
      }
    };

    fetchSmartAssData();
  }, []);

  return (
    <div>
      <h1>Trivial Pursuit With Categories</h1>
      <div className='card-container'>
        {/* Buttons */}
        {displayedCategories.map((category, index) => (
          <button
            className="tpwc-card-button"
            key={index}
            onClick={() => handleClick(category)}
          >
            {category}
          </button>
        ))}

        {/* Modal Popup */}
        {modalContent && (
          <div className="modal">
            <div className="modal-content">
            <button
            style={{
              marginLeft: '5px',
              color: '#007BFF',
              textDecoration: 'none',
              cursor: 'pointer',
              border: 'none',
              background: 'none',
              position: 'absolute',
              left: '0', 

          }} //onClick={flagQuestion}
            
            > Flag Question</button>
              {/* Close (X) Button */}
              <div className="modal-header">
                <span onClick={handleCloseModal} className="close">&times;</span>
                
              </div>

              {/* Modal Content */}
              <p><strong>Category:</strong> {modalContent.category}</p>
              <pre>{modalContent.content}</pre>

              <button onClick={handleCloseModalAndUpdate} className="back" style={{
              color: 'black',
              cursor: 'pointer',
              
              background: 'none',
              left: '0', 

          }} >Next Question</button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};



export { TrivialPursuit, TrivialPursuitWithCats };
