import React, { useState, useEffect } from 'react';
import './cardStyle.css'; // Import CSS for styling
import { fetchData } from './firebase/Read'; // Adjust path as needed
import { saveData, saveSmartAss } from './firebase/Write';
import { httpsCallable } from 'firebase/functions';
import { useAuth } from '../contexts/authContext'
import { functions } from './firebase/firebase';

const SmartAss = () => {
  const [modalContent, setModalContent] = useState(null);
  const [themes, setThemes] = useState([]);
  const [selectedTheme, setSelectedTheme] = useState('');
  const [data, setData] = useState([]);
  const [randomQuestion, setRandomQuestion] = useState(null);
  const [questionCount, setQuestionCount] = useState(1);

  const { currentUser } = useAuth();

  useEffect(() => {
    const fetchSmartAssData = async () => {
      try {
        const result = await fetchData("SmartAss");
        setData(result);
        // Extract unique themes from the dataset
        const uniqueThemes = [...new Set(result.map(item => item.Theme))];
        setThemes(uniqueThemes);
        // console.log('Fetched Data:', result);
        // console.log('Unique Themes:', uniqueThemes);
      } catch (error) {
        console.error("Failed to fetch SmartAss data:", error);
      }
    };

    fetchSmartAssData();
  }, []);


  // Function to handle button clicks
  // const handleClick = (type) => {
  //   const filteredData = data.filter(item => item.CardType === type && item.Theme === selectedTheme);
  //   if (filteredData.length > 0) {
  //     const randomItem = filteredData[Math.floor(Math.random() * filteredData.length)];
  //     //console.log(randomItem)
  //     setRandomQuestion(randomItem);
  //     setModalContent(`${type}: ${randomItem.Answer}\nClues:\n${randomItem.Clues}`);
  //   } else {
  //     setModalContent(`No data found for theme: ${selectedTheme}`);
  //   }
  // };

                const handleClick = async (type) => {
                  //let questionToDisplay;
                  console.log(questionCount)
                  if (currentUser && currentUser.email === 'pilky100@gmail.com' && questionCount % 10 === 0) {
                    console.log('AI')
                    try {
                      const generateTrivia = httpsCallable(functions, 'generate_trivia');
                      const result = await generateTrivia({ 
                        category: type, 
                        role_content: `You are a SmartAss board game card generator. Given the prompted card type, generate one item and 7 clues for the theme ${selectedTheme}.  The clues should start vague and gradually become more specific, with the seventh clue revealing the item's initials (e.g., the initials for Paris, France would be P, F). Respond in JSON format as follows: {"item":"<item>","Clues":"<clues>"}`
                      });

                      if (result.data.error) {
                        setModalContent(result.data.error);
                      } else {
                        const parsedQuestions = JSON.parse(result.data.questions);
                        //questionToDisplay = parsedQuestions[0];
                        
                        const clues =  `${parsedQuestions.clues[0]} \n; ${parsedQuestions.clues[1]} \n; ${parsedQuestions.clues[2]} \n; ${parsedQuestions.clues[3]} \n; ${parsedQuestions.clues[4]} \n; ${parsedQuestions.clues[5]} \n; ${parsedQuestions.clues[6]} \n;` // Access the 'clues' array
                        console.log(clues)

                        setModalContent(`${type}\n ${parsedQuestions.item}\n Clues:\n${clues.replace('; ','')}`);

                        saveSmartAss(
                          "SmartAss", // dbRefPath
                          type,          // game
                          selectedTheme, // category
                          clues, // question
                          parsedQuestions.item    // answer
                        );

                      }
                    } catch (err) {
                      setModalContent('Failed to fetch AI-generated questions. Please try again.');
                      console.error(err);
                      return;
                    }
                  } else {
                    console.log('FB')
                    const filteredData = data.filter(item => item.CardType === type && item.Theme === selectedTheme);
                    if (filteredData.length > 0) {
                      const randomItem = filteredData[Math.floor(Math.random() * filteredData.length)];
                      //console.log(randomItem)
                      setRandomQuestion(randomItem);
                      setModalContent(`${type}: ${randomItem.Answer}\nClues:\n${randomItem.Clues}`);
                    } else {
                      setModalContent(`No data found for theme: ${selectedTheme}`);
                    }
                  }

                  setQuestionCount(questionCount + 1);
                  
                };



   const flagQuestion = () => {
      console.log({randomQuestion});
      saveData(
        "flaggedQuestions", // dbRefPath
        "SmartAss",          // game
        randomQuestion.CardType, // category
        randomQuestion.Clues, // question
        randomQuestion.Answer    // answer
      );
    };

  return (
    <div>
      <h1>SmartAss</h1>
      <div className="theme-selector">
        <label htmlFor="theme">Select Theme:</label>
        <select id="theme" value={selectedTheme} onChange={(e) => setSelectedTheme(e.target.value)}>
          <option value="">--Select a Theme--</option>
          {themes.map((theme, index) => (
            <option key={index} value={theme}>{theme}</option>
          ))}
        </select>
      </div>

      <div className="card-container">
        {/* Buttons */}
        <button id='who-btn' className="card-button" onClick={() => handleClick('Who')}>Who Am I?</button>
        <button id='what-btn' className="card-button" onClick={() => handleClick('What')}>What Am I?</button>
        <button id='where-btn' className="card-button" onClick={() => handleClick('Where')}>Where Am I?</button>

        {/* Modal Popup */}
        {modalContent && (
          <div className="modal">
            <div className="modal-content">
            <button
            style={{
              marginLeft: '5px',
              color: '#007BFF',
              textDecoration: 'none',
              cursor: 'pointer',
              border: 'none',
              background: 'none',
              position: 'absolute',
              left: '0', 

          }} onClick={flagQuestion}
            
            > Flag Question</button>

              <span onClick={() => setModalContent(null)} className="close">&times;</span>
              {modalContent.split('\n').map((line, index) => (
                <p key={index}>{line}</p>
              ))}
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default SmartAss;