import React, { useState } from 'react';
import { doSignInWithEmailAndPassword, doSignInWithGoogle, doSignOut, doCreateUserWithEmailAndPassword } from './firebase/auth'
import { useAuth } from '../contexts/authContext'

const Header = () => {
    const { userLoggedIn } = useAuth();
    const { currentUser } = useAuth();
    //console.log(currentUser)
    
    const [showModal, setShowModal] = useState(false);
    const [showRegisterModal, setShowRegisterModal] = useState(false);
    const [isSigningIn, setIsSigningIn] = useState(false);
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [confirmPassword, setconfirmPassword] = useState('')
    const [isRegistering, setIsRegistering] = useState(false)
    //const [errorMessage, setErrorMessage] = useState('')
    
    const onSubmit = async (e) => {
            e.preventDefault()
            if(!isRegistering) {
                setIsRegistering(true)
                await doCreateUserWithEmailAndPassword(email, password)
            }
        }

    const handleAuth = () => {
        setShowModal(true); // Show modal on click
    };

    const handleRegister = () => {
        setShowModal(false); // Show modal on click
        setShowRegisterModal(true);
    };

    const handleCloseModal = () => {
        setShowModal(false); // Close modal
        setShowRegisterModal(false);
    };


const onGoogleSignIn = (e) => {
        e.preventDefault()
        if (!isSigningIn) {
            setIsSigningIn(true)
            doSignInWithGoogle().catch(err => {
                setIsSigningIn(false)
                setShowModal(false);
            })
        }
    }

    const logIn = async (e) => {
        e.preventDefault()
        if(!isSigningIn) {
            setIsSigningIn(true)
            await doSignInWithEmailAndPassword(email, password)
            // doSendEmailVerification()
            setShowModal(false);
        }
    }

    return (
        <>
            <header style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '10px' }}>
                <h1>Trivia Games!</h1>
                {/* <h1>{userLoggedIn}</h1> */}
                {userLoggedIn ? (
                <p>
                        Logged in as {currentUser.displayName}: 
                        <span
                            onClick={(e) => {
                                doSignOut();
                            }}
                            style={{
                                marginLeft: '5px',
                                color: '#007BFF',
                                textDecoration: 'none',
                                cursor: 'pointer'
                            }}
                        >
                            Logout
                        </span>
                </p>
            ) : (
                <button 
                    onClick={handleAuth} 
                    style={{ padding: '5px 10px', cursor: 'pointer', backgroundColor: '#0095ff', color:'white' }}
                    
                >
                    Login to use AI generated questions
                </button>
            )}
            </header>

            {showModal && (
                <div style={{
                    position: 'fixed',
                    top: 0,
                    left: 0,
                    width: '100%',
                    height: '100%',
                    boxShadow: 'rgba(0, 0, 0)',
                    backgroundColor: 'rgba(0, 0, 0, 0.3)',
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    zIndex: 1000,
                }}>
                    <div style={{
                        background: 'white',
                        padding: '20px',
                        borderRadius: '8px',
                        width: '400px',
                        boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
                    }}>
                        <h2 style={{ textAlign: 'center', marginBottom: '20px' }}>Welcome Back</h2>
                        <label>Email</label>
                        <input type="email" placeholder="Email" style={{ width: '95%', padding: '10px', marginTop: '5px', marginBottom: '10px', borderRadius: '5px', border: 'solid 1px' }} 
                        value={email} onChange={(e) => { setEmail(e.target.value) }}
                        />
                        <label>Password</label>
                        <input type="password" placeholder="Password" style={{ width: '95%', padding: '10px', marginTop: '5px', marginBottom: '20px', borderRadius: '5px', border: 'solid 1px' }} 
                        value={password} onChange={(e) => { setPassword(e.target.value) }}
                        />
                        <button onClick={logIn} style={{
                            width: '100%',
                            padding: '10px',
                            backgroundColor: '#6366F1',
                            color: 'white',
                            border: 'none',
                            borderRadius: '5px',
                            cursor: 'pointer',
                            fontSize: '17px'
                        }}>
                            Sign In
                        </button>
                        <p style={{ textAlign: 'center', marginTop: '10px', fontSize: '14px'}}>
                            Don't have an account? <span onClick={handleRegister} style={{ color: '#6366F1', cursor: 'pointer' }}>Sign up</span>
                        </p>
                        <div style={{ display: 'flex', alignItems: 'center', marginTop: '20px' }}>
                            <div style={{ flex: 1, height: '1px', backgroundColor: '#E5E7EB' }}></div>
                            <span style={{ margin: '0 10px', color: '#9CA3AF' }}>OR</span>
                            <div style={{ flex: 1, height: '1px', backgroundColor: '#E5E7EB' }}></div>
                        </div>
                        <button 
                        onClick={(e) => { onGoogleSignIn(e) }}
                        style={{
                            marginTop: '20px',
                            width: '100%',
                            padding: '10px',
                            border: '1px solid #D1D5DB',
                            borderRadius: '5px',
                            backgroundColor: 'white',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            cursor: 'pointer',
                        }}>
                            <img src="https://www.gstatic.com/firebasejs/ui/2.0.0/images/auth/google.svg" alt="Google" style={{ marginRight: '10px', height: '20px' }} />
                            Continue with Google
                        </button>
                        <button
                            onClick={handleCloseModal}
                            style={{ marginTop: '20px', width: '100%', padding: '10px', background: '#F3F4F6', border: 'none', borderRadius: '5px' }}
                        >
                            Cancel
                        </button>
                    </div>
                </div>
            )}

{showRegisterModal && (
                <div style={{
                    position: 'fixed',
                    top: 0,
                    left: 0,
                    width: '100%',
                    height: '100%',
                    boxShadow: 'rgba(0, 0, 0)',
                    backgroundColor: 'rgba(0, 0, 0, 0.3)',
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    zIndex: 1000,
                }}>
                    <div style={{
                        background: 'white',
                        padding: '20px',
                        borderRadius: '8px',
                        width: '400px',
                        boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
                    }}>
                        <h2 style={{ textAlign: 'center', marginBottom: '20px' }}>Create a New Account</h2>
                        <label>Email</label>
                        <input type="email" placeholder="Email" style={{ width: '95%', padding: '10px', marginTop: '5px', marginBottom: '10px', borderRadius: '5px', border: 'solid 1px' }} 
                        value={email} onChange={(e) => { setEmail(e.target.value) }}
                        />
                        <label>Password</label>
                        <input type="password" placeholder="Password" style={{ width: '95%', padding: '10px', marginTop: '5px', marginBottom: '20px', borderRadius: '5px', border: 'solid 1px' }} 
                        value={password} onChange={(e) => { setPassword(e.target.value) }}
                        />
                        <label>Confirm Password</label>
                        <input type="password" placeholder="Password" style={{ width: '95%', padding: '10px', marginTop: '5px', marginBottom: '20px', borderRadius: '5px', border: 'solid 1px' }} 
                        value={confirmPassword} onChange={(e) => { setconfirmPassword(e.target.value) }}
                        />
                        <button onClick={onSubmit} style={{
                            width: '100%',
                            padding: '10px',
                            backgroundColor: '#6366F1',
                            color: 'white',
                            border: 'none',
                            borderRadius: '5px',
                            cursor: 'pointer',
                            fontSize: '17px'
                        }}>
                            Sign Up
                        </button>
                        
                        <button
                            onClick={handleCloseModal}
                            style={{ marginTop: '20px', width: '100%', padding: '10px', background: '#F3F4F6', border: 'none', borderRadius: '5px' }}
                        >
                            Cancel
                        </button>
                    </div>
                </div>
            )}
        </>
    );
};

export default Header;
